import { VlocityInput } from "../../datasource/request/VlocityInput";

export class NewAttributeAssignmentsInput extends VlocityInput {
  public attrValueMap: any;
  public objectId: string;
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }
  public paramsArray() {
    return [
      "createAttributeAssignments",
      JSON.stringify({ objectId: this.objectId, attributes: this.attrValueMap })
    ];
  }
}
