import { VlocityInput } from "../../datasource/request/VlocityInput";

export class GetObjectPicklistsInput extends VlocityInput {
  public objectName: string;
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public paramsArray() {
    return [
      "getObjectPicklistsByName",
      JSON.stringify({ objectName: this.objectName })
    ];
  }
}
