import { VlocityInput } from "../../../datasource/request/VlocityInput";

/**
 * Class that defines input for applicable objects update for an attribute.
 */
export class UpdateApplicableObjectsInput extends VlocityInput {
  /**
   * attributeId to update applicable object for that attribute
   */
  public attributeId: string;

  /**
   * New objects to map as applicable objects for an attribute
   */
  public newApplicableObjectClasses: any;

  /**
   * Objects to remove from applicable objects for an attribute
   */
  public newNonApplicableObjectClasses: any;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * @return - params for remote action
   */
  public getParamsArray() {
    return [
      "saveApplicableObjectClasses",
      JSON.stringify({
        attributeId: this.attributeId,
        newApplicableObjectClasses: JSON.stringify(
          this.newApplicableObjectClasses
        ),
        newNonApplicableObjectClasses: JSON.stringify(
          this.newNonApplicableObjectClasses
        )
      })
    ];
  }
}
