import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for getting related products required under quick add
 * @extends VlocityInput
 */
export class GetRelatedProductsInput extends VlocityInput {
  /**
   * Id of the root product
   */
  public productId: string;

  /**
   * Search string based on which products are filtered by Name
   */
  public searchString?: string;

  /**
   * Constructor function for getRelatedProducts class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * InputMap for the remote actions call
   */
  public paramsArray() {
    return [
      "getRelatedProducts",
      JSON.stringify({
        productId: this.productId,
        searchString: this.searchString
      })
    ];
  }
}
