import { VlocityInput } from "../../../datasource/request/VlocityInput";

/**
 * Class that defines input for get facet details.
 */

export class GetFacetDetailsInput extends VlocityInput {
  /**
   * ObjectId to get facet details
   */
  public objectId: string;

  /**
   * facetName to get facet details
   */
  public facetName: string;

  constructor(objectId, facetName) {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.objectId = objectId;
    this.facetName = facetName;
  }

  /**
   * @return - params for remote action
   */
  public getParamsArray() {
    return [
      "getFacetsObject",
      JSON.stringify({
        objectId: this.objectId,
        facetName: this.facetName
      })
    ];
  }
}
