import { VlocityInput } from "../../datasource/request/VlocityInput";
import { TimePlanPolicyObject } from "./";
/**
 * This class defines an input for time plan and time policy.
 */
export class GetTimePlanPolicyInput extends VlocityInput {
  /**
   * The parameters required to get time plan and time policy information.
   * The params object should look like below:
   *
   *  id = "01t1H000004adiqQAA"
   */
  public id: TimePlanPolicyObject;

  /**
   * The constructor function that initializes the params to create the body of the GET request.
   * @param {PriceListEntryObject} params - defines the price list entry. For example:
   *
   *  id: = "a2n1H000001DGAuQAO"
   */
  constructor(id: TimePlanPolicyObject) {
    super();

    this.id = id;
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return ["getObject", JSON.stringify({ objectId: this.id })];
  }
}
