import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for parentChildItem.
 * @extends VlocityInput
 */
export class ParentChildItemInput extends VlocityInput {
  /**
   * Id of the record in ParentChildItem table to be deleted
   */
  public parentChildItemId: string = "";

  /**
   * Request body for the record to be updated - Stringified JSON
   */
  public reqBody: string = "";

  /**
   * Constructor function
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * InputMap for deletePCI remote action call
   * @return {string} - stringified json for input
   */
  deleteObjectParamsArray() {
    return [
      "deletePCI",
      JSON.stringify({
        epcRequest: {
          entity: {
            Id: this.parentChildItemId
          }
        }
      })
    ];
  }

  /**
   * InputMap for readPCI remote action call
   * @return {string} - stringified json for input
   */
  getObjectParamsArray() {
    const nsPrefix = SalesForceNamespace.prefix;
    return [
      "readPCI",
      JSON.stringify({
        epcRequest: {
          fieldSet: [
            `Name`,
            `${nsPrefix}CollapseHierarchy__c`,
            `${nsPrefix}IsVirtualItem__c`,
            `${nsPrefix}MinQuantity__c`,
            `${nsPrefix}MaxQuantity__c`,
            `${nsPrefix}Quantity__c`,
            `${nsPrefix}MinimumChildItemQuantity__c`,
            `${nsPrefix}MaximumChildItemQuantity__c`,
            `${nsPrefix}RelationshipType__c`,
            `${nsPrefix}ChildProductId__r.${nsPrefix}ProductSpecId__r.Name`,
            `${nsPrefix}ChildProductId__r.${nsPrefix}VersionLabel__c`,
            `${nsPrefix}ChildProductId__r.${nsPrefix}SpecificationType__c`
          ],
          query: [
            {
              fieldType: "Id",
              comparisonOperator: "equals",
              fieldValue: this.parentChildItemId
            }
          ]
        }
      })
    ];
  }

  /**
   * InputMap for updatePCI remote action call
   * @return {string} - stringified json for input
   */
  updateObjectParamsArray() {
    return [
      "updatePCI",
      JSON.stringify({
        epcRequest: {
          entity: {
            Id: this.parentChildItemId,
            ...JSON.parse(this.reqBody)
          }
        }
      })
    ];
  }
}
