import { SalesForceNamespace } from "../../../common/SalesForceNamespace";
import { VlocityInput } from "../../../datasource/request/VlocityInput";

/**
 * Class that defines input for section eleemnt upsert.
 */
export class UpsertSectionElementInput extends VlocityInput {
  /**
   * ObjectId to create attribute assignment if section element is an attribute
   */
  public objectId: string;

  /**
   * Section element model.
   */
  public sectionElement: any;

  /**
   * Section Id to map to section element and to renumber section elements
   */
  public sectionId: string;

  /**
   * Boolean attribute not to renumber section elements
   */
  public noRenumberElements: boolean;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * @return - params for remote action based on whether its a create or update
   */
  public getParamsArray() {
    const ObjectSectionId = this.sectionElement[
      `${SalesForceNamespace.prefix}ObjectSectionId__c`
    ];
    const params = ObjectSectionId
      ? [
          "updateObject",
          JSON.stringify({
            so: this.getInputMap()
          })
        ]
      : [
          "createObject",
          JSON.stringify({
            objectName: `${SalesForceNamespace.prefix}ObjectElement__c`,
            inputMap: this.getInputMap()
          })
        ];
    return params;
  }

  /**
   * @return - input object for section element upsert
   */
  public getInputMap() {
    let inputMap = {
      Name: this.sectionElement.label
        ? this.sectionElement.label
        : this.sectionElement.Name
    };
    if (this.sectionElement.Id) {
      inputMap["Id"] = this.sectionElement.Id;
    }
    inputMap[`${SalesForceNamespace.prefix}ObjectSectionId__c`] = this.sectionId
      ? this.sectionId
      : this.sectionElement[`${SalesForceNamespace.prefix}ObjectSectionId__c`];
    inputMap[`${SalesForceNamespace.prefix}Sequence__c`] = this.sectionElement[
      `${SalesForceNamespace.prefix}Sequence__c`
    ];
    inputMap[`${SalesForceNamespace.prefix}Column__c`] = this.sectionElement[
      `${SalesForceNamespace.prefix}Column__c`
    ]
      ? this.sectionElement[`${SalesForceNamespace.prefix}Column__c`].toString()
      : "1";
    inputMap[
      `${SalesForceNamespace.prefix}IsRequired__c`
    ] = this.sectionElement.isRequired;
    if (
      this.sectionElement.propertyType === "Field" ||
      this.sectionElement[`${SalesForceNamespace.prefix}Type__c`] === "Field"
    ) {
      inputMap[`${SalesForceNamespace.prefix}FieldApiName__c`] = this
        .sectionElement[`${SalesForceNamespace.prefix}FieldApiName__c`]
        ? this.sectionElement[`${SalesForceNamespace.prefix}FieldApiName__c`]
        : this.sectionElement.Name;
    } else {
      inputMap[`${SalesForceNamespace.prefix}AttributeId__c`] =
        this.sectionElement.AttributeId ||
        this.sectionElement[`${SalesForceNamespace.prefix}AttributeId__c`];
    }
    inputMap[`${SalesForceNamespace.prefix}Type__c`] = this.sectionElement[
      `${SalesForceNamespace.prefix}Type__c`
    ]
      ? this.sectionElement[`${SalesForceNamespace.prefix}Type__c`]
      : this.sectionElement.propertyType;
    return inputMap;
  }
}
