export class SimpleProductSearchInput {
  public searchText: string;
  public fields: string[];

  public query() {
    if (!this.fields || this.fields.length === 0) {
      this.fields = ["Id, Name"];
    }
    const fields = this.fields.join(",");
    return `FIND {${
      this.searchText
    }} IN NAME FIELDS RETURNING Product2(${fields})`;
  }
}
