import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { getInputMap } from "../common/SearchUtils";
/**
 * Class that defines input for getting attachments.
 */
export class GetVlocityAttachmentsInput extends VlocityInput {
  /**
   * Object Id of the product
   */
  public productId: string;

  /**
   * Global group key of Product
   */
  public productGroupkey: string;

  /**
   * sortObject - containts sortBy and SortOrder
   */
  public sortObject: any;

  /**
   * limit - limit for pagination
   */
  public pageSize: any;

  /**
   * searchString - searchString to search in attachments
   */
  public searchString: string;

  /**
   * isDefault - isDefault to get only default image
   */
  public isDefault;

  /**
   * pageNumber - pageNumber for pagination.
   */
  public pageNumber;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.sortObject = {};
  }

  /**
   * Function that constructs an array for getVlocityAttachments remote action
   * @returns {Array}
   */
  public getParamsArray() {
    const searchSpecs = [
      {
        logicalOperator: "or",
        comparisonOperator: "equals",
        query: `${SalesForceNamespace.prefix}ObjectId__c`,
        queryValue: this.productId,
        searchable: true,
        arrayIndex: 0
      },
      {
        query: "Name",
        queryValue: "searchStr",
        logicalOperator: "or",
        comparisonOperator: "like",
        searchable: this.searchString && this.searchString.length > 2,
        arrayIndex: 1
      },
      {
        query: `${SalesForceNamespace.prefix}ContentType__c`,
        queryValue: "searchStr",
        logicalOperator: "or",
        comparisonOperator: "like",
        searchable: this.searchString && this.searchString.length > 2,
        arrayIndex: 1
      }
    ];
    if (this.productGroupkey) {
      searchSpecs.push({
        logicalOperator: "or",
        comparisonOperator: "equals",
        query: `${SalesForceNamespace.prefix}ProductGroupKey__c`,
        queryValue: this.productGroupkey,
        searchable: true,
        arrayIndex: 0
      });
    }
    if (this.isDefault) {
      searchSpecs.push({
        query: `${SalesForceNamespace.prefix}IsDefaultImage__c`,
        queryValue: this.isDefault,
        logicalOperator: "and",
        comparisonOperator: "equals",
        searchable: true,
        arrayIndex: 2
      });
    }
    const searchParams = {
      orderBy: this.sortObject.sortBy
        ? `${this.sortObject.sortBy} ${
            this.sortObject.sortOrder ? this.sortObject.sortOrder : "ASC"
          } NULLS LAST`
        : `${
            SalesForceNamespace.prefix
          }SequenceNumber__c NULLS LAST,CreatedDate ${
            this.sortObject.sortOrder ? this.sortObject.sortOrder : "ASC"
          }`,
      pageSize: this.pageSize ? this.pageSize : 10,
      pageNumber: this.pageNumber ? this.pageNumber : 1,
      searchKey: this.searchString
    };
    const inputMap = getInputMap(searchSpecs, searchParams);
    return ["readVlocityAttachment", inputMap];
  }
}
