  private getRemoteInput(input, inputClass) {
    if (
      !input ||
      !input.remoteClass ||
      !input.remoteMethod ||
      !input.paramsArray()
    ) {
      throw new Error(
        `${inputClass} class is required. The input must have a remoteClass, remoteMethod, all parameters and paramsArray()`
      );
    }
    return new ApexRemoteInput({
      className: input.remoteClass,
      methodName: input.remoteMethod,
      paramsArray: input.paramsArray()
    });
  }
