import { AttributeValue } from "./AttributeValue";

export class Attribute {
  public id: string;
  public dataType: string;
  public inputType: string;
  public multiselect: boolean;
  public required: string;

  public placeholder: string;
  public maxlength: number; // integer max
  public minlength: number; // integer min
  public min: number; // decimal min
  public max: number; // decimal max
  public pattern: string;
  public autocomplete: boolean;
  public step: number; // decimal step
  public label: string;
  public formatMask: string;
  public hasRules: boolean;
  public customTemplate: string;
  public disabled: boolean;
  public hidden: boolean;
  public filterable: boolean;
  public cloneable: boolean;
  public description: string;
  public values: AttributeValue[];

  public isRatingAttribute: boolean;
  public code: string;
  public rules: any;
}
