import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for getting product definition.
 * @extends VlocityInput
 */
export class GetProductDefinitionInput extends VlocityInput {
  /**
   * Product Id to be used in the associated API to get corresponding data record.
   */
  public productId: string;

  public facet: string;

  /**
   * Constructor function for GetProductDefinitionInput class
   */
  constructor() {
    super();
    this.remoteClass = "PCAppHandler";
    this.remoteMethod = "getFacetsObject";
  }
}
