  public createCreateChildProductInput(): CreateChildProductInput {
    return new CreateChildProductInput();
  }

  /**
   * @method createChildProduct - creates a new child product as provided in the CreateChildProductInput
   *
   * @example
   * ```typescript
   *
   *    // Instantiate the input object for createChildProduct using createParentChildItemInput method
   *    const input = productconsole.getProductStructureSdk().createParentChildItemInput();
   *
   *    // Invoke API via method createChildProduct()
   *    productconsole
   *   .getProductStructureSdk().createChildProduct(input)
   *   .then(result => {
   *     Logger.info("createChildProduct() success " + result);
   *   })
   *   .catch(error => {
   *     Logger.info("createChildProduct() failed " + error);
   *   });
   * ```
   * <pre>API Path: (PATCH) `/services/data/v43.0/sobjects/{namespacePrefix}ProductChildItem__c/{input.parentChildItemId}`</pre>
   * @param {CreateChildProductInput} input
   * @return {Promise<Result>} - A promise that resolves to the ID of the newly created relationship
   * @throws {Error} - "CreateChildProductInput :: createChildProduct() must be defined"
   * @throws {Error} - "CreateChildProductInput :: createChildProduct() must have parentProductId"
   * @throws {Error} - "CreateChildProductInput :: createChildProduct() must have childProductId"
   * @throws {Error} - "CreateChildProductInput :: createChildProduct() must have relationshipType as 'Child' "
   */
  public async createChildProduct(
    input: CreateChildProductInput
  ): Promise<Result> {
    if (!input.sequence) {
      throw new Error(
        "CreateChildProductInput :: createChildProduct() must be defined"
      );
    }
    if (!input.parentProductId) {
      throw new Error(
        "CreateChildProductInput :: createChildProduct() must have parentProductId"
      );
    }
    if (!input.childProductId) {
      throw new Error(
        "CreateChildProductInput :: createChildProduct() must have childProductId"
      );
    }
    if (input.relationshipType !== "Child") {
      throw new Error(
        "CreateChildProductInput :: createChildProduct() must have relationshipType as 'Child' "
      );
    }

    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: input.remoteClass,
      methodName: input.remoteMethod,
      paramsArray: input.paramsArray()
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

