import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that contains all input parameters to update a product attribute.
 */
export class UpdateProductAttributeInput extends VlocityInput {
  /**
   * Product Id to be used in the associated API to update corresponding data record.
   */
  public productId: string;

  /**
   * Attribute data in json format.
   */
  public productAttribute: any;

  /**
   * Constructor function for UpdateProductAttributeInput class
   */
  constructor() {
    super();
    this.remoteClass = "PCAppHandler";
    this.remoteMethod = "updateAttributesProduct";
  }
}
