import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { NewPromotionItemObject } from "./";
/**
 * This class defines an input for creating a promotion.
 */
export class CreatePromotionItemInput extends VlocityInput {
  /**
   * The parameters required to create a promotion item.
   * The params object should look like below:
   *  params = {
   *      promotionId : "a2tf2000001c5BM",
   *      actionType : "Add",
   *      cardinalityCheckScope : null,
   *      contextEntityFilterId : null,
   *      contextProductId : null,
   *      description : "Quad Play Fall Promo",
   *      effectiveFromDate : "2018-01-01T08:00:00.000+0000",
   *      effectiveUntilDate : "2025-12-31T08:00:00.000+0000",
   *      isActive : true,
   *      isUndoable : false,
   *      maxQuantity : 2.0,
   *      minQuantity : 1.0,
   *      offerId : null,
   *      productId : "01tf2000007Mmu4AAC",
   *      quantity : 1.0,
   *      undoableMessage : null,
   *      updateScope : "Update Item Only"
   *   }
   */
  public params: NewPromotionItemObject;

  /**
   * The constructor function that initializes the params to create the body of the PUT request.
   * @param {NewPromotionItemObject} params - defines the new promotion item. For example:
   *  params = {
   *      promotionId : "a2tf2000001c5BM",
   *      actionType : "Add",
   *      cardinalityCheckScope : null,
   *      contextEntityFilterId : null,
   *      contextProductId : null,
   *      description : "Quad Play Fall Promo",
   *      effectiveFromDate : "2018-01-01T08:00:00.000+0000",
   *      effectiveUntilDate : "2025-12-31T08:00:00.000+0000",
   *      isActive : true,
   *      isUndoable : false,
   *      maxQuantity : 2.0,
   *      minQuantity : 1.0,
   *      offerId : null,
   *      productId : "01tf2000007Mmu4AAC",
   *      quantity : 1.0,
   *      undoableMessage : null,
   *      updateScope : "Update Item Only"
   *   }
   */
  constructor(params: NewPromotionItemObject) {
    super();

    this.params = params;
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public getNewPromotionItemInputString(): object {
    const nsPrefix = SalesForceNamespace.prefix;
    const entityObj = {};
    entityObj[nsPrefix + "PromotionId__c"] = this.params.promotionId;
    entityObj[nsPrefix + "ActionType__c"] = this.params.actionType;
    entityObj[
      nsPrefix + "CardinalityCheckScope__c"
    ] = this.params.cardinalityCheckScope;
    entityObj[
      nsPrefix + "ContextEntityFilterId__c"
    ] = this.params.contextEntityFilterId;
    entityObj[nsPrefix + "ContextProductId__c"] = this.params.contextProductId;
    entityObj[nsPrefix + "Description__c"] = this.params.description;
    entityObj[nsPrefix + "ProductHierarchyPath__c"] = this.params.hierarchyPath;
    entityObj[
      nsPrefix + "ProductHierarchyGroupKeyPath__c"
    ] = this.params.productHierarchyGroupKeyPath;
    entityObj[
      nsPrefix + "ProductHierarchyGlobalKeyPath__c"
    ] = this.params.productHierarchyGlobalKeyPath;
    entityObj[nsPrefix + "IsActive__c"] = this.params.isActive;
    entityObj[nsPrefix + "IsUndoable__c"] = this.params.isUndoable;
    entityObj[nsPrefix + "MaxQuantity__c"] = this.params.maxQuantity;
    entityObj[nsPrefix + "MinQuantity__c"] = this.params.minQuantity;
    entityObj[nsPrefix + "ProductId__c"] = this.params.productId;
    entityObj[nsPrefix + "Quantity__c"] = this.params.quantity;
    entityObj[nsPrefix + "UndoableMessage__c"] = this.params.undoableMessage;
    entityObj[nsPrefix + "UpdateScope__c"] = this.params.updateScope;

    if (this.params.offerId) {
      entityObj[nsPrefix + "OfferId__c"] = this.params.offerId;
    }
    return {
      epcRequest: {
        entity: entityObj
      }
    };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return [
      "addPromotionItem",
      JSON.stringify(this.getNewPromotionItemInputString())
    ];
  }
}
