import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for getting quick add properties.
 * @extends VlocityInput
 */
export class GetQuickAddProductPropertiesInput extends VlocityInput {
  /**
   * Object id of the product.
   */
  public productId: string;

  public facet: string;

  /**
   * Constructor function for GetQuickAddProductPropertiesInput class
   */
  constructor() {
    super();
    this.remoteClass = "PCAppHandler";
    this.remoteMethod = "getAvailableFacetsObject";
  }

  /**
   * Function that constructs the query string for API
   * @returns {Object}
   */
  public inputMap() {
    return {
      facet: this.facet,
      productId: this.productId
    };
  }
}
