import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for getting override
 * @extends VlocityInput
 */
export class GetOverrideInput extends VlocityInput {
  /**
   * This will be Attribute for attributes and Product Definition for PCI.
   */
  public type: any;

  /**
   * Id of the bundle
   */
  public contextId: string;

  /**
   * Id of AttributeAssignment
   */
  public overriddenObjectId: string;

  /**
   * Id of the PCI
   */
  public overriddenPCIId: string;

  /**
   * Id of the PromotionItem
   */
  public promotionItemId: string;

  /**
   * Id of the Promotion
   */
  public promotionId: string;

  /**
   * Hierarchy path for the product
   */
  public hierarchyPath: string;

  /**
   * Hierarchy path of Global Group Key for the product
   */
  public productHierarchyGroupKeyPath: string;

  /**
   * Constructor function for GetOverrideInput class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.type = "Attribute";
    this.promotionItemId = null;
    this.promotionId = null;
  }

  /**
   * InputMap for the remote actions call
   */
  public paramsArray() {
    return [
      "getOverrideWithExcludedValues",
      JSON.stringify({
        type: this.type,
        contextId: this.contextId,
        overriddenObjectId: this.overriddenObjectId,
        overriddenPCIId: this.overriddenPCIId,
        promotionItemId: this.promotionItemId,
        promotionId: this.promotionId,
        hierarchyPath: this.hierarchyPath,
        productHierarchyGroupKeyPath: this.productHierarchyGroupKeyPath
      })
    ];
  }
}
