import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { EditPromotionOfferPriceListEntryObject } from "./";
/**
 * This class defines an input to edit a promotion or offer price list entry.
 */
export class GetEditPromotionOfferPriceListEntryInput extends VlocityInput {
  /**
   * The parameters required to edit a promotion or offer price list entry.
   * The params object should look like below:
   *  params = {
   *      promotionId: "a2tf2000001aTGIAA2",
   *      promotionItemId: "a2sf2000006hgXtAAI",
   *      pricingElementId: "a2if200000DezzG",
   *      priceListId: "a2gf2000000EPmI",
   *      offerId: "01tf2000007Mmu4AAC",
   *      displayText: "iPhone Fall Adjustment",
   *      timePlanId: "01tf2000007Mmu4OKH"
   *      timePolicyId: "01tf2000007Mmu4EUH"
   *      isOverride: true | false,
   *      isBasePrice: true | false
   *  }
   */
  public params: EditPromotionOfferPriceListEntryObject;

  /**
   * The constructor function that initializes the params to create the body of the PUT request.
   * @param {EditPromotionOfferPriceListEntryObject} params - defines the adjustment. For example:
   *  params = {
   *      promotionId: "a2tf2000001aTGIAA2",
   *      promotionItemId: "a2sf2000006hgXtAAI",
   *      pricingElementId: "a2if200000DezzG",
   *      priceListId: "a2gf2000000EPmI",
   *      offerId: "01tf2000007Mmu4AAC",
   *      displayText: "iPhone Fall Adjustment",
   *      timePlanId: "01tf2000007Mmu4OKH"
   *      timePolicyId: "01tf2000007Mmu4EUH"
   *      isOverride: true | false,
   *      isBasePrice: true | false
   *  }
   */
  constructor(params: EditPromotionOfferPriceListEntryObject) {
    super();

    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.params = params;
  }

  public getPriceListInputString(): object {
    const nsPrefix = SalesForceNamespace.prefix;
    const entityObj = {};
    entityObj["Id"] = this.params.priceListEntryId;
    entityObj[nsPrefix + "PromotionId__c"] = this.params.promotionId;
    entityObj[nsPrefix + "PricingElementId__c"] = this.params.pricingElementId;
    entityObj[nsPrefix + "TimePolicyId__c"] = this.params.timePolicyId;
    entityObj[nsPrefix + "TimePlanId__c"] = this.params.timePlanId;
    entityObj[nsPrefix + "OfferId__c"] = this.params.offerId;
    entityObj[nsPrefix + "IsBasePrice__c"] = this.params.isBasePrice;
    entityObj[nsPrefix + "IsVirtualPrice__c"] = !this.params.isBasePrice;
    entityObj[nsPrefix + "IsOverride__c"] = this.params.isOverride;
    entityObj[nsPrefix + "DisplayText__c"] = this.params.displayText;
    entityObj[nsPrefix + "IsActive__c"] = this.params.isActive;
    entityObj[nsPrefix + "EffectiveFromDate__c"] = this.params.startDate;
    entityObj[nsPrefix + "EffectiveUntilDate__c"] = this.params.endDate;
    return {
      epcRequest: {
        entity: entityObj
      }
    };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return [
      "updatePriceListEntry",
      JSON.stringify(this.getPriceListInputString())
    ];
  }
}
