import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { ContextRuleObject } from "./";
/**
 * This class defines an input for a price list.
 */
export class AddContextRuleSetInput extends VlocityInput {
  /**
   * The parameters required to create a price list entry.
   * The params object should look like below:
   *  params = {
   *      objectId: "01t1H000004adiqQAA",
   *      ruleId: "a2n1H000001DGAuQAO"
   *  }
   */
  public params: ContextRuleObject;

  /**
   * The constructor function that initializes the params to create the body of the PUT request.
   * @param {ContextRuleObject} params - defines what pricing variable to look for. For example:
   *  params = {
   *      objectId: "01t1H000004adiqQAA",
   *      ruleId: "a2n1H000001DGAuQAO"
   *  }
   */
  constructor(params: ContextRuleObject) {
    super();
    this.params = params;
    this.apiNamespace = `${SalesForceNamespace.prefix}__` || "";
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public getContextRuleSetInputString(): object {
    return {
      epcRequest: {
        entity: JSON.parse(`{
            "${SalesForceNamespace.prefix}ObjectId__c": "${
          this.params.objectId
        }",
            "${SalesForceNamespace.prefix}RuleId__c": "${this.params.ruleId}"
        }`)
      }
    };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return [
      "addPricingContextRule",
      JSON.stringify(this.getContextRuleSetInputString())
    ];
  }
}
