import { SalesForceNamespace } from "../../../common/SalesForceNamespace";
import { VlocityInput } from "../../../datasource/request/VlocityInput";

/**
 * Class that defines input for create/update section.
 */
export class UpsertSectionInput extends VlocityInput {
  /**
   * Object facet id to map in Vlocity section with facet
   */
  public objectFacetId: string;

  /**
   * Section model
   */
  public section: any;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * @return - params array for remote action
   */
  public getParamsArray() {
    if (this.section && this.section.Id) {
      return [
        "updateObject",
        JSON.stringify({
          so: this.getInputMap()
        })
      ];
    } else {
      return [
        "createObject",
        JSON.stringify({
          objectName: `${SalesForceNamespace.prefix}ObjectSection__c`,
          inputMap: this.getInputMap()
        })
      ];
    }
  }

  /**
   * @return - section Object input to create/update Section object
   */
  public getInputMap() {
    let inputMap: any = {};
    if (this.section.Id) {
      inputMap.Id = this.section.Id;
    }
    inputMap[`${SalesForceNamespace.prefix}SectionId__c`] = this.section[
      `${SalesForceNamespace.prefix}SectionId__c`
    ];
    inputMap[`${SalesForceNamespace.prefix}ColumnLayout__c`] = this.section[
      `${SalesForceNamespace.prefix}ColumnLayout__c`
    ]
      ? this.section[`${SalesForceNamespace.prefix}ColumnLayout__c`]
      : "1 Column";
    inputMap[`${SalesForceNamespace.prefix}TabKeyOrder__c`] = this.section[
      `${SalesForceNamespace.prefix}TabKeyOrder__c`
    ]
      ? this.section[`${SalesForceNamespace.prefix}TabKeyOrder__c`]
      : "Top-Down";
    inputMap[`${SalesForceNamespace.prefix}ObjectFacetId__c`] = this.section[
      `${SalesForceNamespace.prefix}ObjectFacetId__c`
    ]
      ? this.section[`${SalesForceNamespace.prefix}ObjectFacetId__c`]
      : this.objectFacetId;
    return inputMap;
  }
}
