import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for creating child products.
 * @extends VlocityInput
 */
export class CreateChildProductInput extends VlocityInput {
  /**
   * Object Id of the parent product
   */
  public parentProductId: string;

  /**
   * Object Id of the child product
   */
  public childProductId: string;

  /**
   * Relationship type between the parent and the child product
   */
  public relationshipType: string;

  /**
   * Child line number for the relationship
   */
  public sequence: number;

  /**
   * Constructor function for CreateChildProductInput class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * InputMap for the remote actions call
   * @return {string} - stringified json for input
   */
  public paramsArray() {
    const objName = `${SalesForceNamespace.prefix}ProductChildItem__c`;
    const inputDetails = {};
    inputDetails[
      `${SalesForceNamespace.prefix}ParentProductId__c`
    ] = this.parentProductId;
    inputDetails[
      `${SalesForceNamespace.prefix}ChildProductId__c`
    ] = this.childProductId;
    inputDetails[
      `${SalesForceNamespace.prefix}RelationshipType__c`
    ] = this.relationshipType;
    inputDetails[
      `${SalesForceNamespace.prefix}ChildLineNumber__c`
    ] = this.sequence;

    return [
      "createPCI",
      JSON.stringify({
        epcRequest: {
          entity: { ...inputDetails }
        },
        objectType: objName
      })
    ];
  }
}
