import { SalesForceNamespace } from "../../common";
export class DeleteVlocityAttachmentInput {
  /**
   * Object Id of the attachment
   */
  public attachmentIds;

  constructor(attachmentIds) {
    this.attachmentIds = attachmentIds;
  }

  /**
   * Method to form EPC request to delete multiple/single vlocity attachment records.
   */
  getInputMap() {
    const request = {
      epcRequest: {
        entities: []
      }
    };
    this.attachmentIds.forEach(attachment => {
      const entity = {
        Id: attachment.Id
      };
      entity[`${SalesForceNamespace.prefix}ObjectId__c`] = attachment.objectId;
      request.epcRequest.entities.push({ entity });
    });
    return ["deleteVlocityAttachment", JSON.stringify(request)];
  }
}
