/**
 * Class that defines input for getting products for quick add under structure.
 * @extends VlocityInput
 */
export class GetProductsInput {
  /**
   * Gets the list of products.
   * @return {string} - query string to get a list of products of Product2 object. Each record has Name, Id and Description of the product.
   */
  public getQuery(): string {
    return `SELECT Name, Id, Description FROM Product2`;
  }
}
