import { SalesForceNamespace } from "../../common";
/**
 * Class that defines input for getting Id for Product2 object.
 * @extends VlocityInput
 */
export class GetProduct2ObjectInput {
  /**
   * Gets the Id for Product2 object.
   * @return {string} - query string to get Id for Product2 object.
   */
  public getQuery(): string {
    return `SELECT Id from ${
      SalesForceNamespace.prefix
    }ObjectClass__c where Name='Product2 Object'`;
  }
}
