import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";

export class SearchRelatedObjectsInput extends VlocityInput {
  /**
   * Search text. Use % to search all records.
   */
  public searchString: string;

  /**
   * Name of the salesforce object. For example, Account or AttributeAssignment__c
   */
  public objectName: string;

  /**
   * Specification type filter
   * @expectedValues are 'Product'/'Resource'/'Service'/'AllSpecs'. AllSpecs will get all spec products.
   * If its not send or null, we would get all Product2 records.
   */
  public specificationTypeFilter?: string;

  /**
   * Page number to fetch.
   */
  public pageNumber: number;

  /**
   * Specify the field set name from the FieldSettings__c object.
   */
  public fieldSetName: string;

  /**
   * Number of records per page.
   */
  public pageSize: number;

  /**
   * Selected Object Class Id to filter object types while selecting parent object type.
   */
  public selectedObjectClassId: string;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }
  public paramsArray() {
    let inputMap: any = {
      fieldSetName: this.fieldSetName,
      objectType: this.objectName,
      pageNumber: this.pageNumber,
      pageSize: this.pageSize,
      searchString: this.searchString,
      specificationTypeFilter: this.specificationTypeFilter
    };
    if (
      this.objectName === "ObjectClass__c" ||
      this.objectName === `${SalesForceNamespace.prefix}ObjectClass__c`
    ) {
      inputMap.condition = `${
        SalesForceNamespace.prefix
      }ObjectApiName__c = 'Product2' AND ${
        SalesForceNamespace.prefix
      }IsActive__c = true`;
      inputMap.condition = this.selectedObjectClassId
        ? inputMap.condition + ` AND Id!='${this.selectedObjectClassId}'`
        : inputMap.condition;
    }
    return ["getSearchResultsMap", JSON.stringify({ inputMap })];
  }
}
