import { VlocityInput } from "../../datasource/request/VlocityInput";

export class GetTimePlanPolicyIdsInput extends VlocityInput {
  /**
   * The parameters required get time plan and time policy.
   * The params object should look like below:
   *  params = {
   *      StartTimePolicy: "Purchase Date",
   *      EndTimePolicy: "End of Plan Duration",
   *      Frequency: 3,
   *      TimePlanUOM: "Days",
   *      Delay: 4,
   *      DelayUOM: "Weeks"
   *  }
   */
  public params: object;

  /**
   * The constructor function that initializes remoteClass, remoteMethod, and the parameters used for the ApexRemote call.
   * @param {object} params - defines what pricing variable to look for. For example:
   *  params = {
   *      StartTimePolicy: "Purchase Date",
   *      EndTimePolicy: "End of Plan Duration",
   *      Frequency: 3,
   *      TimePlanUOM: "Days",
   *      Delay: 4,
   *      DelayUOM: "Weeks"
   *  }
   */
  constructor(params: object) {
    super();

    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.params = params;
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return ["findMatchingTimePlanTimePolicy", JSON.stringify(this.params)];
  }
}
