import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for deleting override
 * @extends VlocityInput
 */
export class DeleteOverrideInput extends VlocityInput {
  /**
   * This will be Attribute for attributes and Product Definition for PCI.
   */
  public type: any;

  /**
   * Id of the bundle
   */
  public contextId: string;

  /**
   * Id of AttributeAssignment
   */
  public overridingObjectId: string;

  /**
   * Id of OverrideDefinition
   */
  public overrideDefinitionId: string;

  /**
   * Constructor function for DeleteOverrideInput class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.type = "Attribute";
  }

  /**
   * InputMap for the remote actions call
   */
  public paramsArray() {
    return [
      "deleteOverride",
      JSON.stringify({
        type: this.type,
        contextId: this.contextId,
        overridingObjectId: this.overridingObjectId,
        overrideDefinitionId: this.overrideDefinitionId
      })
    ];
  }
}
