import { SalesForceNamespace } from "../../common/SalesForceNamespace";

/**
 * Represents Version of an object.
 */
export class Version {
  /**
   * Name of the new version
   */
  name: string;

  /**
   * Record id of the object from which the new version is being created.
   */
  objectId: string;

  /**
   * Version start date
   */
  startDate;

  /**
   * Version end date
   */
  endDate;

  /**
   * Object name of the object from which the new version is being created.
   */
  objectApiName: string;

  /**
   * @returns - returns the request for new version creation.
   */
  getCreateRequestBody() {
    const nsp = SalesForceNamespace.prefix;
    let request: any = {};
    request[nsp + "VersionLabel__c"] = this.name;
    request.objectId = this.objectId;
    request.objectName = this.objectApiName;
    return request;
  }
}
