import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * Class that defines input for getting spec details of a product
 * @extends VlocityInput
 */
export class GetSpecDetailsInput extends VlocityInput {
  /**
   * Id of the Product
   */
  public objectId: string;

  /**
   * Constructor function for GetSpecDetailsInput class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * InputMap for the remote actions call
   */
  public paramsArray() {
    return ["getSpecDetails", JSON.stringify({ objectId: this.objectId })];
  }
}
