import { VlocityInput } from "../../datasource/request/VlocityInput";

export class UpdateObjectInput extends VlocityInput {
  public objectId: string;
  public fieldValueMap: any;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }
  public paramsArray() {
    return [
      "updateObject",
      JSON.stringify({ objectId: this.objectId, so: this.fieldValueMap })
    ];
  }
}
