/**
 * This class defines an input for creating a promotion.
 */
export class NewPromotionObject {
  /**
   * The price list id that the promotion will be based on.
   */

  public priceListId: string;
  /**
   * The name of the promotion.
   */
  public name: string;

  /**
   * The promotion code.
   */
  public promoCode: string;

  /**
   * Determines if the promotion is active.
   */
  public isActive: boolean;
}
