import { VlocityInput } from "../../datasource/request/VlocityInput";

export class UpdateAttributeAssignmentsInput extends VlocityInput {
  public attributeAssignments: any;
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }
  public paramsArray() {
    return [
      "updateAttributeAssignments",
      JSON.stringify({ attributes: this.attributeAssignments })
    ];
  }
}
