import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * A class that defines an input for getting the quick adds pricing properties.
 * @extends VlocityInput
 */
export class GetQuickAddPricingPropertiesInput extends VlocityInput {
  public type: string;
  public productId: string;
  /**
   * GetQuickAddPricingPropertiesInput constructor.
   */
  constructor() {
    super();

    this.apiNamespace = SalesForceNamespace.namespace;
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public getQuickAddInput() {
    return this.type === "Rule"
      ? {
          inputMap: {
            objectType: `${SalesForceNamespace.prefix}Rule__c`,
            searchString: "%",
            condition: `${
              SalesForceNamespace.prefix
            }RuleType__c='Qualification'`
          }
        }
      : {
          inputMap: {
            objectType: `${SalesForceNamespace.prefix}Promotion__c`,
            searchString: "%",
            condition: `Id NOT IN (SELECT ${
              SalesForceNamespace.prefix
            }PromotionId__c FROM ${
              SalesForceNamespace.prefix
            }PromotionItem__c WHERE ${
              SalesForceNamespace.prefix
            }ProductId__c = '${this.productId}')`
          }
        };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return ["getSearchResultsMap", JSON.stringify(this.getQuickAddInput())];
  }
}
