import { VlocityInput } from "../../datasource/request/VlocityInput";

export class SaveAttributeAssignmentInput extends VlocityInput {
  public attributeAssignment: any;
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public paramsArray() {
    return [
      "saveAttributeAssignment",
      JSON.stringify({
        so: this.attributeAssignment
      })
    ];
  }
}
