/**
 * Class that defines input for creating documents.
 */
export class CreateSalesforceDocumentInput {
  /**
   * List of uploaded files.
   */
  public files: any[];

  /**
   * Folder id to upload documents to.
   */
  public folderId;

  /**
   * Creates a Document object and maps values from the passed input
   * @return {Object} - returns Document object
   */
  public formDocumentObject(file): any {
    const documentRecord = {
      Type: file.name ? file.name.split(".").pop() : "",
      Name: file.name,
      FolderId: this.folderId,
      Description: "Product Console File Upload",
      ContentType: file.type,
      Body: file.base64String
    };
    return documentRecord;
  }

  /**
   * Method to form EPC request to create multiple Salesforce document records.
   * @return {Object} - returns EPC request.
   */
  public getInputMap() {
    const request = {
      epcRequest: {
        entities: []
      }
    };
    this.files.forEach(file => {
      const documentRecord = this.formDocumentObject(file);
      request.epcRequest.entities.push({ entity: documentRecord });
    });
    return request;
  }
}
