import { VlocityInput } from "../../datasource/request/VlocityInput";

/**
 * A class that defines an input for getting a promotions list.
 * @extends VlocityInput
 */
export class GetAppliedPromotionsInput extends VlocityInput {
  /**
   * Object id of the product.
   */
  public productId: string;
  /**
   * Object id of the root offer.
   */
  public offerId: string;
  public sortBy: string;

  constructor() {
    super();

    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return [
      "getPromotionsProduct",
      JSON.stringify({
        id: this.productId,
        ...(this.sortBy && { sortBy: this.sortBy }),
        ...(this.offerId && { offerIds: this.offerId })
      })
    ];
  }
}
