import { VlocityInput } from "../../datasource/request/VlocityInput";

export class UpdateSingleAttributeInput extends VlocityInput {
  public attributeId: string;
  public productId: string;
  public isVisible: boolean;
  public isConfigurable: boolean;
  public isReadonly: boolean;
  public isActive: boolean;
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }
  public paramsArray() {
    return [
      "updateSingleAttribute",
      JSON.stringify({
        attributeFields: {
          IsConfigurable: this.isConfigurable,
          IsHidden: !this.isVisible,
          IsReadOnly: this.isReadonly,
          IsActive: this.isActive
        },
        attributeId: this.attributeId,
        objectId: this.productId
      })
    ];
  }
}
