import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { NewPromotionObject } from "./";
/**
 * This class defines an input for creating a promotion.
 */
export class CreatePromotionInput extends VlocityInput {
  /**
   * The parameters required to create a promotion.
   * The params object should look like below:
   *  params = {
   *      priceListId: "a2l1H00000F71qLQAR",
   *      promoCode: "59684678",
   *      name: "iPhone X",
   *      isActive: true | false
   *  }
   */
  public params: NewPromotionObject;

  /**
   * The constructor function that initializes the params to create the body of the PUT request.
   * @param {NewPromotionObject} params - defines the new promotion. For example:
   *  params = {
   *      priceListId: "a2l1H00000F71qLQAR",
   *      promoCode: "59684678",
   *      name: "iPhone X",
   *      isActive: true | false
   *  }
   */
  constructor(params: NewPromotionObject) {
    super();
    this.params = params;
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public getNewPromotionInputString(): object {
    return {
      epcRequest: {
        entity: JSON.parse(`{
          "${SalesForceNamespace.prefix}PriceListId__c": "${
          this.params.priceListId
        }",
          "${SalesForceNamespace.prefix}Code__c": "${this.params.promoCode}",
          "Name": "${this.params.name}",
          "${SalesForceNamespace.prefix}IsActive__c": ${this.params.isActive}
        }`)
      }
    };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return ["addPromotion", JSON.stringify(this.getNewPromotionInputString())];
  }
}
