import { VlocityInput } from "../../datasource/request/VlocityInput";

export class GetProductAttributeAssignmentsInput extends VlocityInput {
  public productId: string;

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  public paramsArray() {
    return [
      "getAttributeAssignments",
      JSON.stringify({ objectId: this.productId })
    ];
  }
}
