import { SalesForceNamespace } from "../../common";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { EditPriceListEntryObject } from "./";
/**
 * This class defines an input for editing a price list entry.
 */
export class GetEditPriceListEntryInput extends VlocityInput {
  /**
   * The parameters required to edit a price list entry.
   * The params object should look like below:
   *  params = {
   *      productId: "01t1H000004adiqQAA",
   *      pricingElementId: "a2n1H000001DGAuQAO",
   *      priceListId: "a2l1H00000F71qLQAR",
   *      displayText: "iPhone X",
   *      isBasePrice: true | false
   *  }
   */
  public params: EditPriceListEntryObject;

  /**
   * The constructor function that initializes remoteClass, remoteMethod, and the parameters used for the ApexRemote call.
   * @param {object} params - defines what pricing variable to look for. For example:
   *  params = {
   *      productId: "01t1H000004adiqQAA",
   *      pricingElementId: "a2n1H000001DGAuQAO",
   *      priceListId: "a2l1H00000F71qLQAR",
   *      displayText: "iPhone X",
   *      isBasePrice: true | false
   *  }
   */
  constructor(params: EditPriceListEntryObject) {
    super();

    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.params = params;
  }

  /**
   * The constructor function that initializes the params to create the body of the PUT request.
   * @param {EditPriceListEntryObject} params - defines the price list entry. For example:
   *  params = {
   *      productId: "01t1H000004adiqQAA",
   *      pricingElementId: "a2n1H000001DGAuQAO",
   *      priceListId: "a2l1H00000F71qLQAR",
   *      displayText: "iPhone X",
   *      isBasePrice: true | false
   *  }
   */

  public getPriceListInputString(): object {
    const nsPrefix = SalesForceNamespace.prefix;
    const entityObj = {};
    entityObj["Id"] = this.params.priceListEntryId;
    entityObj[nsPrefix + "ProductId__c"] = this.params.productId;
    entityObj[nsPrefix + "PricingElementId__c"] = this.params.pricingElementId;
    entityObj[nsPrefix + "DisplayText__c"] = this.params.displayText;
    entityObj[nsPrefix + "IsBasePrice__c"] = this.params.isBasePrice;
    entityObj[nsPrefix + "IsVirtualPrice__c"] = this.params.isVirtualPrice;
    entityObj[nsPrefix + "IsActive__c"] = this.params.isActive;
    entityObj[nsPrefix + "EffectiveFromDate__c"] = this.params.startDate;
    entityObj[nsPrefix + "EffectiveUntilDate__c"] = this.params.endDate;
    return {
      epcRequest: {
        entity: entityObj
      }
    };
  }

  /**
   * Constructs the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    return [
      "updatePriceListEntry",
      JSON.stringify(this.getPriceListInputString())
    ];
  }
}
