import { ApexRemoteInput } from "../../datasource/apexremote";
import { UpdateApplicableObjectsInput } from "../vlocityattributes";

export class VlocityAttributes {
  public config;

  constructor(config) {
    this.config = config;
  }

  /**
   * @method getObjects - get objects.
   *
   * @example
   * ```typescript
   *
   *    // objectId as parameter to the getObjects method
   *    const attributeId = "";
   *    // Invoke API via method getObjects()
   *    productconsole
   *   .getVlocityAttributesSdk().getObjects(attributeId)
   *   .then(result => {
   *     Logger.info("vlocity objects remote action success " + result);
   *   })
   *   .catch(error => {
   *     Logger.info("vlocity objects remote action failed " + error);
   *   });
   * ```
   *
   * @param {attributeId} input - input.
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   * @throws {Error} - "attributeId :: getObjects() must be defined"
   */
  public getObjects(attributeId) {
    const allObjects = this.getAllObjects();
    const applicableObjects = this.getApplicableObjects(attributeId);
    return new Promise((resolve, reject) => {
      Promise.all([allObjects, applicableObjects])
        .then(values => {
          resolve(this.transformObjects(values[0].result, values[1].result));
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  /**
   * @method getAllObjects - returns all object classes
   *
   * @example
   * ```typescript
   *
   *    // Invoke API via method getAllObjects()
   *    productconsole
   *   .getVlocityAttributesSdk().getAllObjects()
   *   .then(result => {
   *     Logger.info("getAllObjects() remote action success " + result);
   *   })
   *   .catch(error => {
   *     Logger.info("getAllObjects() remote action failed " + error);
   *   });
   * ```
   *
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   */
  public async getAllObjects() {
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: ["getAllObjectClasses", JSON.stringify({})]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

  /**
   * @method getApplicableObjects - returns applicable object classes for an attribute
   *
   * @example
   * ```typescript
   *
   *    // Invoke API via method getApplicableObjects()
   *    const attributeId = "";
   *    productconsole
   *   .getVlocityAttributesSdk().getApplicableObjects(attributeId)
   *   .then(result => {
   *     Logger.info("getApplicableObjects() remote action success " + result);
   *   })
   *   .catch(error => {
   *     Logger.info("getApplicableObjects() remote action failed " + error);
   *   });
   * ```
   *
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   * @throws {Error} - "attributeId :: getApplicableObjects() must be defined"
   */
  public async getApplicableObjects(attributeId) {
    if (!attributeId) {
      throw new Error("attributeId :: getApplicableObjects() must be defined");
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: [
        "getApplicableObjectClasses",
        JSON.stringify({ attributeId: attributeId })
      ]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

  /**
   * @method transformObjects - returns modified allObjects and applied objects
   * @return - modified allObjects and applied objects
   */
  public transformObjects(allObjects, appliedObjects) {
    if (appliedObjects && appliedObjects.length) {
      appliedObjects.forEach(appliedObject => {
        const object = allObjects.find(obj => obj.Id === appliedObject.Id);
        if (object) {
          object.isSelected = true;
        }
      });
    }
    return {
      allObjects: allObjects,
      appliedObjects: appliedObjects
    };
  }

  /**
   * @method saveApplicableObjects - update applicable objects
   *
   * @example
   * ```typescript
   *
   *    // Instantiate the input object for saveApplicableObjects using createUpdateApplicableObjectsInput() to specify parameters
   *    // attributeId, newApplicableObjectClasses and newNonApplicableObjectClasses as parameters to the createGetFacetDetailsInput
   *    const input = productconsole.getVlocityAttributesSdk.createGetFacetDetailsInput();
   *    input.attributeId = "";
   *    input.newApplicableObjectClasses = [];
   *    input.newNonApplicableObjectClasses = [];
   *    // Invoke API via method saveApplicableObjects()
   *    productconsole
   *   .getVlocityAttributesSdk().saveApplicableObjects(input)
   *   .then(result => {
   *     Logger.info("vlocity save applicable objects remote action success " + result);
   *   })
   *   .catch(error => {
   *     Logger.info("vlocity save applicable objects remote action failed " + error);
   *   });
   * ```
   *
   * @param {UpdateApplicableObjectsInput} input - input.
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   * @throws {Error} - "UpdateApplicableObjectsInput :: saveApplicableObjects() must be defined"
   * @throws {Error} - "UpdateApplicableObjectsInput :: saveApplicableObjects() must have attributeId defined"
   * @throws {Error} - "UpdateApplicableObjectsInput :: saveApplicableObjects() must have newApplicableObjectClasses defined"
   * @throws {Error} - "UpdateApplicableObjectsInput :: saveApplicableObjects() must have newNonApplicableObjectClasses defined"
   */
  public async saveApplicableObjects(input: UpdateApplicableObjectsInput) {
    if (!input) {
      throw new Error(
        "UpdateApplicableObjectsInput :: saveApplicableObjects() must be defined"
      );
    }
    if (!input.attributeId) {
      throw new Error(
        "UpdateApplicableObjectsInput :: saveApplicableObjects() must have attributeId defined"
      );
    }
    if (!input.newApplicableObjectClasses) {
      throw new Error(
        "UpdateApplicableObjectsInput :: saveApplicableObjects() must have newApplicableObjectClasses defined"
      );
    }
    if (!input.newNonApplicableObjectClasses) {
      throw new Error(
        "UpdateApplicableObjectsInput :: saveApplicableObjects() must have newNonApplicableObjectClasses defined"
      );
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: input.remoteClass,
      methodName: input.remoteMethod,
      paramsArray: input.getParamsArray()
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

  /**
   * Returns the instance of UpdateApplicableObjectsInput to update applicable objects
   * @return {UpdateApplicableObjectsInput}
   */
  public createUpdateApplicableObjectsInput(): UpdateApplicableObjectsInput {
    return new UpdateApplicableObjectsInput();
  }
}
