  /**
   * This function returns the default input object which can be used for calling getQuickAddProductProperties sdk function.
   * @return {GetQuickAddProductPropertiesInput}
   */
  public createQuickAddProductPropertiesInput(): GetQuickAddProductPropertiesInput {
    return new GetQuickAddProductPropertiesInput();
  }

  /**
   * This function returns a list of attributes, fields and attribute values that are available for assignment to a given product.
   * <pre>API Path: /v1/studio/object/{input.productid}/facet/{input.facet}/available.</pre>
   * @param {GetQuickAddProductPropertiesInput} input - Input object that consists of a productId.
   * @return {Promise<Result>} - A promise that resolves to the list of available fields, attributes and attribute values which can be applied to the product.
   */
  public async getQuickAddProductProperties(
    input: GetQuickAddProductPropertiesInput
  ): Promise<Result> {
    const apexRestInput: ApexRestInput = new ApexRestInput({
      method: HttpMethod.GET,
      path: `${SalesForceNamespace.restAPIPrefix}/v1/studio/object/${
        input.productId
      }/facets/${input.facet}/available`
    });

    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: input.remoteClass,
      inputMap: input.inputMap(),
      methodName: input.remoteMethod
    });
    return await this.config.datasource
      .dual(apexRestInput, apexRemoteInput)
      .execute();
  }

  /**
   * This function creates and returns a default input object that can be used to call the getProductDefinition sdk function.
   * @return {GetProductDefinitionInput} - Creates and returns an instance of input class for getProductDefinition.
   */
  public createGetProductDefinitionInput(): GetProductDefinitionInput {
    return new GetProductDefinitionInput();
  }

  /**
   * Gets the product definition for a given product. Definition contains a list of sections and section elements within each section.
   * <pre>API Path: (GET) /v1/studio/object/{input.productId}/facets/{input.facetName}</pre>
   * @param {GetProductDefinitionInput} input - input with the product id to fetch the product definition
   * @return {Promise<Result>} - prommise that resolves to the product definition which is a list of sections. Each section has a `sectionElements.records` which represent the fields and attributes.
   */
  public async getProductDefinition(
    input: GetProductDefinitionInput
  ): Promise<Result> {
    if (!input.productId || !input.productId.trim()) {
      throw new Error("Product Id is required to get proudct defintion.");
    }
    const apexRestInput: ApexRestInput = new ApexRestInput({
      method: HttpMethod.GET,
      path: `${SalesForceNamespace.restAPIPrefix}/v1/studio/object/${
        input.productId
      }/facets${input.facet ? "/" + input.facet : ""}`
    });

    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: input.remoteClass,
      inputMap: {
        productId: input.productId
      },
      methodName: input.remoteMethod
    });
    return await this.config.datasource
      .dual(apexRestInput, apexRemoteInput)
      .execute();
  }

  /**
   * Creates and returns a default instance of input object for calling updateProductDefinition sdk function.
   * @return {UpdateProductDefinitionInput} - Input object for calling updateProductDefinition sdk function.
   */
  public createUpdateProductDefintionInput(): UpdateProductDefinitionInput {
    return new UpdateProductDefinitionInput();
  }

  /**
   * Posts the updated product definition (sections and sectionElements) to the api endpoint for updating a product.
   * <pre>API Path: (PUT) /v1/studio/object/{input.productId}/facets/{input.facet}</pre>
   * @param { UpdateProductDefinition } input - Input class for product that has the product definition.
   * @return {Promise<Result>} - Promise that resolves to the updated product definition.
   */
  public async updateProductDefinition(
    input: UpdateProductDefinitionInput
  ): Promise<Result> {
    if (!input.productId || !input.productId.trim()) {
      throw new Error(
        "Product Id is required in order to update product definition"
      );
    }

    const apexRestInput: ApexRestInput = new ApexRestInput({
      body: input.productDefinition,
      method: HttpMethod.PUT,
      path: `${SalesForceNamespace.restAPIPrefix}/v1/studio/object/${
        input.productId
      }/facets${input.facet ? "/" + input.facet : ""}`
    });

    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: input.remoteClass,
      inputMap: {
        productDefinition: input.productDefinition,
        productId: input.productId
      },
      methodName: input.remoteMethod
    });
    return await this.config.datasource
      .dual(apexRestInput, apexRemoteInput)
      .execute();
  }

