  /**
   * Posts an update to the standard salesforce REST API for updating a single field on the Product2 Object.
   * <pre>API Path: (PATCH) /services/data/v43.0/sobjects/Product2/{input.productId}</pre>
   * @param {UpdateProductFieldInput} input
   * @return {Promise<Result>} - If update is successful, this would promise would have no content as API returns HTTP-204 (No content)
   */
  public async updateProductField(
    input: UpdateProductFieldInput
  ): Promise<Result> {
    if (!input.fieldName) {
      throw new Error("A field name is required for this operation");
    }

    const reqBody = {};
    reqBody[input.fieldName] = input.fieldValue;
    const restInput: RestInput = new RestInput({
      options: {
        body: JSON.stringify(reqBody),
        headers: {
          Authorization: `Bearer ${this.config.datasource.config.sessionId}`,
          "Content-Type": "application/json"
        },
        method: HttpMethod.PATCH
      },
      url: `${
        this.config.datasource.config.url
      }/services/data/v56.0/sobjects/Product2/${input.productId}`
    });
    return await this.config.datasource.rest(restInput).execute();
  }

