  /**
   * Uploads files specified in the FormData to SalesForce Documents section. Request body is of type multipart/form-data
   * <pre>API Path: (POST) /services/data/v43.0/composite/sobjects</pre>
   * @return {Promise<BulkCreateResult[]>} - Promise that resolves to a list of records that has the document ids of each of the uploaded documents.
   */
  public async uploadToSalesforce(input: CreateSalesforceDocumentInput) {
    if (!input) {
      throw new Error(
        "CreateSalesforceDocumentInput :: uploadToSalesforce() must be defined"
      );
    }
    if (!input.folderId) {
      throw new Error(
        "CreateSalesforceDocumentInput :: folder id must be defined to upload salesforce documents"
      );
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: ["addDocument", JSON.stringify(input.getInputMap())]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

