  /**
   * Returns the folderId for a given folder name. FolderId is required when creating documents.
   * <pre> API Path: (GET) /services/data/v43.0/query </pre>
   * @param {string} folderName
   * @return {Promise<Result>} - Promise that resolves to a list of records that match the folder name. Each record has Id and Name of the folder.
   */
  public async getFolderByName(folderName): Promise<Result> {
    if (!folderName) {
      throw new Error("folderName :: getFolderByName() must be defined");
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: [
        "readEntity",
        JSON.stringify({
          epcRequest: {
            query: [
              {
                fieldValue: folderName,
                comparisonOperator: "equals",
                fieldType: "Name"
              }
            ]
          },
          objectType: "Folder"
        })
      ]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

