  /**
   * Get Vlocity Attachment details
   * @param {GetVlocityAttachmentDetailsInput} input - The input is used to get the attachment details.
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   */
  public async getVlocityAttachmentDetails(
    input: GetVlocityAttachmentDetailsInput
  ): Promise<Result> {
    if (!input) {
      throw new Error("GetVlocityAttachmentDetailsInput is required as input");
    }
    if (input && !input.attachmentId) {
      throw new Error("Attachment id is required to fetch details");
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: [
        "readVlocityAttachment",
        JSON.stringify(input.getInputMap())
      ]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

  /**
   * Get Vlocity Attachment details
   * @param {CreateVlocityAttachmentsInput} input - The input is used to update the attachment details.
   * @return {Promise<Result>} - Promise that returns a success or failure message.
   */
  public async updateVlocityAttachment(
    input: CreateVlocityAttachmentsInput
  ): Promise<Result> {
    if (!input) {
      throw new Error("CreateVlocityAttachmentsInput is required as input");
    }
    if (!input.records || input.records.length === 0) {
      throw new Error(
        "CreateVlocityAttachmentsInput.records array should have the VlocityAttachment object to update"
      );
    }
    if (input.records && input.records.length > 0 && !input.records[0].Id) {
      throw new Error(
        "VlocityAttachment object in CreateVlocityAttachmentsInput.records array should have the id to update, if its a new record use createVlocityAttachments()"
      );
    }
    const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: [
        "updateVlocityAttachment",
        JSON.stringify(input.getInputMap())
      ]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }

