  /**
   * Returns the list of most recently viewed products ordered descending by Last viewed date.
   */
  public async getRecentlyViewedProducts() {
    const soqlInput: SoqlInput = new SoqlInput({
      query: `SELECT Name, Id
        FROM Product2 WHERE LastViewedDate != NULL
        ORDER BY LastViewedDate DESC`
    });
    return await this.config.datasource.soql(soqlInput).execute();
  }

