import { ApexRemoteInput } from "../../datasource/apexremote";
import ProductConsoleSDKConfig from "../ProductConsoleSDKConfig";
import { Version } from "./Version";

/**
 * Exposes SDK functions for Versioning functionalities
 */
export class Versioning {
  public config: ProductConsoleSDKConfig;
  constructor(config: ProductConsoleSDKConfig) {
    this.config = config;
  }

  /**
   * Creates an instance of Version object and returns it.
   *
   * @example
   * ```typescript
   * const versionSdk = VlocitySDK.productconsole.getInstance().getVersioningSdk();
   * const version = versionSdk.getVersionObject();
   * version.name = "Version 1";
   * ```
   *
   * @returns {Version}
   */
  public getVersionObject(): Version {
    return new Version();
  }

  /**
   * Function to create a newversion of an object.
   * @param verion:Version  - Version instance with all values like name, startDate and endDate
   *
   * @example
   * ```typescript
   * const versionSdk = VlocitySDK.productconsole.getInstance().getVersioningSdk();
   * const version = versionSdk.getVersionObject();
   * version.name="Version 1";
   * version.objectAPpiName="Product2";
   * version.objectId = "00DFEV23423";
   * versionSdk.createVersion(version).then(result=>{
   *  Logger.info("create version remote action success " + result);
   * }).catch(error=>{
   *  Logger.info("create version remote action failure " + error);
   * })
   * ```
   *
   * @returns {Promise<>}
   * @throws {Error} - "Object Id and Object name are required to get object details by id."
   */
  public async createVersion(version: Version) {
    if (!version) {
      throw new Error(
        "createVersion :: Instance of Version must be passed as input"
      );
    }

    if (!version.objectId) {
      throw new Error("createVersion :: version.objectId must be defined");
    }

    if (!version.name) {
      throw new Error("createVersion :: version.name must be defined");
    }

    let apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
      className: "ProductConsoleControllerV2",
      methodName: "invokeMethod",
      paramsArray: [
        "createNewVersion",
        JSON.stringify({
          inputMap: version.getCreateRequestBody()
        })
      ]
    });
    return await this.config.datasource.apexRemote(apexRemoteInput).execute();
  }
}
