import { SalesForceNamespace } from "../../common/SalesForceNamespace";
import { VlocityInput } from "../../datasource/request/VlocityInput";
import { queryStringEncode } from "../../utils";

/**
 * Class that defines input for getting product details.
 * @extends VlocityInput
 */
export class GetProductDetailsInput extends VlocityInput {
  /**
   * Product Id to be used in the associated API to get corresponding product and its children.
   * If this is set, the API will return a single record.
   */
  public productId?: string;

  /**
   * List of fields to be returned.
   */
  public fields?: string[];

  /**
   * Constructor function for GetProductDetailsInput class
   */
  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
  }

  getParamsArray() {
    const nsPrefix = SalesForceNamespace.prefix;
    return [
      "readProduct2",
      JSON.stringify({
        epcRequest: {
          fieldSet: [
            `${nsPrefix}SpecificationSubType__c`,
            `${nsPrefix}GlobalKey__c`,
            `${nsPrefix}ProductSpecId__r.Name`,
            `${nsPrefix}ProductSpecId__r.${nsPrefix}SpecificationType__c`,
            `${nsPrefix}ProductSpecId__r.${nsPrefix}SpecificationSubType__c`,
            `${nsPrefix}ProductSpecId__r.${nsPrefix}GlobalGroupKey__c`,
            `${nsPrefix}ProductSpecId__r.${nsPrefix}GlobalKey__c`
          ],
          include: ["productHierarchy"],
          query: [
            {
              fieldValue: this.productId,
              comparisonOperator: "equals",
              fieldType: "Id"
            }
          ]
        }
      })
    ];
  }

  /**
   * Function that constructs the query string for API
   * @returns {Object}
   */
  public inputMap(): any {
    return {
      fields: this.fields.join(","),
      id: this.productId
    };
  }

  /**
   * Function that constructs the API path
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = queryStringEncode(this.inputMap(), true);
    return `${
      SalesForceNamespace.restAPIPrefix
    }/v1/studio/products/${encodedQueryString}`;
  }
}
