function getInputMap(searchSpecs, searchParams) {
  let epcRequest: any = {};
  epcRequest.include = searchParams.include ? searchParams.include : [];
  epcRequest.pageInfo = {
    orderBy: searchParams.orderBy,
    pageSize: searchParams.pageSize,
    pageNumber: searchParams.pageNumber
  };
  epcRequest.querySet = getQuerySet(searchSpecs, searchParams);
  return JSON.stringify({ epcRequest });
}

function getQuerySet(searchSpecs, searchParams) {
  let query = [];
  searchSpecs.forEach(column => {
    if (column.searchable) {
      let obj: any = {};
      obj.fieldType = column.query;
      obj.comparisonOperator = column.comparisonOperator;
      obj.logicalOperator = column.logicalOperator;
      if (column.queryValue !== "searchStr") {
        obj.fieldValue = column.queryValue;
      } else if (searchParams.searchKey && searchParams.searchKey.length) {
        let searchKeyArray = searchParams.searchKey.split(/[\s,/]+/);
        if (searchKeyArray.length > 1) {
          obj = getQuerySetRecordsForMultiWordSearchKey(obj, searchKeyArray);
        } else {
          obj.fieldValue = searchParams.searchKey;
          obj = [obj];
        }
      }
      query[column.arrayIndex] = query[column.arrayIndex]
        ? [].concat(query[column.arrayIndex], obj)
        : [].concat(obj);
    }
  });
  let querySet = { and: [] };
  query.forEach(eachQuery => {
    if (eachQuery && eachQuery.length) {
      querySet.and.push(eachQuery);
    }
  });
  return querySet;
}

function getQuerySetRecordsForMultiWordSearchKey(obj, searchKeyArray) {
  let querySetRecords = [];
  searchKeyArray.forEach(str => {
    if (str) {
      let record: any = {};
      record.fieldType = obj.fieldType;
      record.comparisonOperator = obj.comparisonOperator;
      record.logicalOperator = obj.logicalOperator;
      record.fieldValue = str;
      querySetRecords.push(record);
    }
  });
  return querySetRecords;
}

export { getInputMap };
