import { SalesForceNamespace } from "../../common";
import { SoqlInput } from "../../datasource/soql/SoqlInput";

/**
 * This class defines an input for a price list.
 */
export class GetPriceListInput extends SoqlInput {
  /**
   * This method returns the query to get price list.
   */
  public query: string;

  constructor(activeOnly: boolean) {
    super({});
    this.query = `SELECT Id, Name, ${
      SalesForceNamespace.prefix
    }Description__c FROM ${SalesForceNamespace.prefix}PriceList__c ${
      activeOnly ? `WHERE ${SalesForceNamespace.prefix}IsActive__c = true` : ""
    }`;
  }
}
