import { SalesForceNamespace } from "../../common";

/**
 * Class that defines input for attachments.
 */
export class CreateVlocityAttachmentsInput {
  /**
   * List of VlocityRecords attachment.
   */
  public records: any[];

  /**
   * Object Id of the product
   */
  public productId: string;

  /**
   * Global group key of product.
   */
  public productGroupKey: string;

  /**
   * Creates a VlocityAttachment object and maps attribute values from the passed input
   * @return {Object} - returns VlocityAttachment object
   */
  public formVlocityAttachmentObject(record): any {
    const namespacePrefix = SalesForceNamespace.prefix
      ? `${SalesForceNamespace.prefix}`
      : "";
    const attachmentRecord: any = {};
    attachmentRecord.Name = record.Name ? record.Name : record.documentId;
    attachmentRecord[`${namespacePrefix}IsDefaultImage__c`] =
      record[`${namespacePrefix}IsDefaultImage__c`];
    attachmentRecord[`${namespacePrefix}ContentType__c`] =
      record[`${namespacePrefix}ContentType__c`];
    attachmentRecord[`${namespacePrefix}Description__c`] =
      record[`${namespacePrefix}Description__c`];
    attachmentRecord[`${namespacePrefix}UrlLong__c`] = record[
      `${namespacePrefix}UrlLong__c`
    ]
      ? record[`${namespacePrefix}UrlLong__c`]
      : record.documentId
        ? `/servlet/servlet.FileDownload?file=${record.documentId}`
        : null;
    attachmentRecord[`${namespacePrefix}AttachmentId__c`] =
      record[`${namespacePrefix}AttachmentId__c`];
    attachmentRecord[`${namespacePrefix}SequenceNumber__c`] =
      record[`${namespacePrefix}SequenceNumber__c`];
    attachmentRecord[`${namespacePrefix}ObjectId__c`] = record[
      `${namespacePrefix}ObjectId__c`
    ]
      ? record[`${namespacePrefix}ObjectId__c`]
      : this.productId;
    if (record.Id) {
      attachmentRecord.Id = record.Id;
    } else {
      if (this.productGroupKey) {
        attachmentRecord[
          `${namespacePrefix}ProductGroupKey__c`
        ] = this.productGroupKey;
      }
    }
    return attachmentRecord;
  }

  /**
   * Method to form epc request to create multiple vlocity attachments
   * @return {Object} - returns EPC request with vlocity attachment objects.
   */
  public getInputMap() {
    const request = {
      epcRequest: {
        entities: []
      }
    };
    this.productGroupKey = this.productGroupKey || this.getStoredGroupKey();
    this.records.forEach(record => {
      const attachmentRecord = this.formVlocityAttachmentObject(record);
      request.epcRequest.entities.push({ entity: attachmentRecord });
    });
    return request;
  }

  /**
   * Method to get group key stored in browser's session storage.
   * @return {Object} - returns Global group key of product.
   */
  public getStoredGroupKey() {
    const groupKey = window.sessionStorage.getItem(
      `groupkey-${this.productId}`
    );
    return groupKey || null;
  }
}
