import { SalesForceNamespace } from "../../common/SalesForceNamespace";
/**
 * Base class that defines an input
 */
export class VlocityInput {
  /**
   * Remote class that handles this request.
   */
  public remoteClass: string;

  /**
   * Remote method that handles this request.
   */
  public remoteMethod: string;

  /**
   * Rest URL that handles this request.
   */
  public restUrl: string;

  /**
   * Apex Rest URL that handles this request.
   */
  public apexRestUrl: string;
  public apexRestUrlAWS: string;
  /**
   * Salesforce organization.
   */
  public apiNamespace: string = SalesForceNamespace.restAPIPrefix;

  /**
   * A method that returns the end point with rest url and or namepsace.
   *
   * @return {string} - endpoint with rest url and or apiNamespace.
   */
  public getEndPointWithNamespace() {
    if (!this.restUrl.startsWith("/")) {
      this.restUrl = `/${this.restUrl}`;
    }
    return `/services/apexrest${SalesForceNamespace.restAPIPrefix}${
      this.restUrl
    }`;
  }

  /**
   * A method that returns the end point with apex rest url with namepsace if defined.
   *
   * @return {string} - endpoint with apex rest url with apiNamespace if defined.
   */
  public getApexRestEndPoint() {
    if (!this.apexRestUrl) {
      throw new Error(
        "VlocityInput.apexRestUrl is invalid and must be defined."
      );
    }

    if (!this.apexRestUrl.startsWith("/")) {
      this.apexRestUrl = `/${this.restUrl}`;
    }

    return `${SalesForceNamespace.restAPIPrefix}${this.apexRestUrl}`;
  }
}
