export class GetVlocityAttachmentDetailsInput {
  /**
   * object id of the vlocity attachment
   */
  public attachmentId: string;

  /**
   * Method to form request to get Vlocity Attachment details.
   * @return {Object} : EPC Request object with mapped atttachment Id.
   */
  getInputMap() {
    return {
      epcRequest: {
        query: [
          {
            fieldValue: this.attachmentId,
            comparisonOperator: "equals",
            fieldType: "Id"
          }
        ]
      }
    };
  }
}
